@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PT-Mono";

  font-display: swap;
  src:
    url("../fonts/pt-mono_regular.woff") format("woff"),
    url("../fonts/pt-mono_regular.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";

  font-display: swap;
  src:
    url("../fonts/roboto-condensed-bold.woff") format("woff"),
    url("../fonts/roboto-condensed-bold.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";

  font-display: swap;
  src:
    url("../fonts/roboto-condensed-regular.woff") format("woff"),
    url("../fonts/roboto-condensed-regular.woff2") format("woff2");
}

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Roboto";

  font-display: swap;
  src:
    url("../fonts/roboto-medium.woff") format("woff"),
    url("../fonts/roboto-medium.woff2") format("woff2");
}

$basic-font: "PT-Mono", "Arial", sans-serif;
$additional-font: "Roboto Condensed", "Arial", sans-serif;
$highlight-font: "Roboto", "Arial", sans-serif;
